//====================================================================================================
.chat {
	background: rgb(37, 37, 37);
	color: #fff;
	&__container {
		position: relative;
		padding-top: rem(30);
	}
	&__row {
		max-width: 550px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		margin: 0 auto;
	}
	&__items {
		height: calc(100vh - 30px);
		overflow: auto;
		display: flex;
		flex-direction: column;
		width: 100%;
		padding-bottom: rem(85);
		gap: 10px;
	}
	&__item {
		display: flex;
		&.current {
			justify-content: flex-end;
		}
	}
	&__inner {
		padding: 10px 15px;
		border-radius: 10px 10px 10px 0px;
		background: #000;
		display: inline-flex;
		flex-direction: column;
		flex: 0 0 auto;
	}
	&__user {
		font-size: rem(12);
		color: red;
		margin-bottom: rem(10);
	}
	&__message {
		font-size: rem(16);
	}
	&__bottom {
		display: flex;
		align-items: center;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: rem(80);
		margin: 0 auto;
		justify-content: center;
		background: rgb(37, 37, 37);
		padding: rem(15);
	}
	&__btn {
		button {
			background: #000;
			border-radius: rem(5);
			padding: 10px 15px;
			min-height: 40px;
			transition: all 0.3s ease 0s;
			&:hover {
				background: rgb(17, 17, 17);
			}
		}
	}
	&__input {
		flex: 0 0 rem(450);
		padding-right: rem(15);
		input {
			background: transparent !important;
			border-radius: rem(5) !important;
			min-height: rem(40);
			border: 2px solid #000;
			padding: 5px 15px;
			width: 100%;
			outline: none !important;
			color: #fff;
		}
	}
}
